import { Link, NavLink } from "react-router-dom";
import React from "react";
import Logo from "../logo.png";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Image from "react-bootstrap/Image";

class MainNavbar extends React.Component {
  render() {
    const linkativo = {
      background: "red",
      color: "white"
    };
    return (
      <Navbar fixed="top" bg="light" expand="lg">
        <Navbar.Brand href="#home">
          <Link to="/">
            <Image src={Logo} fluid thumbnail className="Logo" />
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <NavLink to="/compreaqui" activeStyle={linkativo}>
              Compre Aqui
            </NavLink>
            <NavLink to="/camarotes" activeStyle={linkativo}>
              Camarotes
            </NavLink>
            <NavLink to="/blocos" activeStyle={linkativo}>
              Blocos
            </NavLink>
            <NavLink to="/navios" activeStyle={linkativo}>
              Navios
            </NavLink>
            <NavLink to="/hoteis" activeStyle={linkativo}>
              Hotéis
            </NavLink>
            <NavLink to="/atendimento" activeStyle={linkativo}>
              Atendimento
            </NavLink>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default MainNavbar;
