import { Link } from "react-router-dom";
import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class BottomNavbar extends React.Component {
  render() {
    return (
      <Navbar fixed="bottom" bg="light" expand="lg">
        <FontAwesomeIcon icon={["fas", "comments"]} size="lg" />
        <FontAwesomeIcon icon={["far", "comments"]} size="lg" />
        <FontAwesomeIcon icon={["fab", "whatsapp"]} size="lg" />
        <Link to="/compreaqui">
          <Button variant="success">Compre aqui</Button>
        </Link>
      </Navbar>
    );
  }
}

export default BottomNavbar;
