import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React, { Suspense, lazy } from "react";
import "./App.css";
import MainNavbar from "./components/MainNavbar.jsx";
import BottomNavbar from "./components/BottomNavbar.jsx";
import UserBar from "./components/UserBar.jsx";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import Container from "react-bootstrap/Container";

const Home = lazy(() => import("./routes/Home"));
const CompreAqui = lazy(() => import("./routes/CompreAqui"));
const Camarotes = lazy(() => import("./routes/Camarotes"));
const Blocos = lazy(() => import("./routes/Blocos"));
const Navios = lazy(() => import("./routes/Navios"));
const Hoteis = lazy(() => import("./routes/Hoteis"));
const Atendimento = lazy(() => import("./routes/Atendimento"));

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.setState({});
  }
  render() {
    library.add(fab, fas, far);
    return (
      <div className="App">
        <Router>
          <header className="App-header">
            <MainNavbar></MainNavbar>
          </header>
          <Container>
            <UserBar />

            <Suspense fallback={<div>Carregando...</div>}>
              <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/compreaqui" component={CompreAqui} />
                <Route path="/camarotes" component={Camarotes} />
                <Route path="/blocos" component={Blocos} />
                <Route path="/navios" component={Navios} />
                <Route path="/hoteis" component={Hoteis} />
                <Route path="/atendimento" component={Atendimento} />
              </Switch>
            </Suspense>
          </Container>
          <footer className="App-footer">
            <BottomNavbar></BottomNavbar>
          </footer>
        </Router>
      </div>
    );
  }
}

export default App;
